<template>
	<div class="end-preview" v-loading="load" :element-loading-text="text">
		<div>
			<p>企业360度画像预览已结束，如需继续浏览内容，敬请购买</p>
			<p class='free-limit'>¥20.00 限免 <i></i></p>
			<p class="download" @click='download'>下载画像</p>
		</div>
		
	</div>
</template>

<script>
	export default {
	    name: 'endPreview',
	    data () {
	    	return {
		    	load:false,
		    	text:'努力加载中',
		    	id:'',
		    	name:''
		    }
		},
		mounted:function(){
			let n=this.$route.params;
			this.id=n.id;
			this.name=n.name;
		},	
		methods:{
			download(){
				let vm=this;
				this.load=true;
				
				var iframe = document.createElement("iframe"); 
				iframe.style.display="none";
				document.body.appendChild(iframe); 
				
				//http://202.106.10.250:18069  http://192.168.20.93:8069
				let src = `/portrait/entPortrait/downLoad/${this.id}/${this.name}`;
				iframe.src = src
				
				iframe.onload=function(){
					vm.text='下载失败';
				}
				
				this.$ajax.get(src).then(function(res){
					//vm.text='下载成功';
					setTimeout(function(){vm.load=false},1200)
				},function(err){
					//vm.text='下载失败';
					setTimeout(function(){vm.load=false},1200)
				})
			}
		}
  	}
</script>

<style scoped>
.end-preview{
	height:100%;
 	display:-webkit-box;
 	-webkit-box-align:center;
 	-webkit-box-pack:center;
}
.end-preview p{
	text-align: center;
	line-height:3;
}
.free-limit{
	font-size: 16px;
	color:#ff9517;
	font-weight: bold;
	position:relative;
}
.free-limit i{
 	width:58px;
 	position: absolute;
 	border-top:1px solid #ff9517;
 	left:37%;
 	top:24px;
 }
 .download{
 	width:108px;
 	height:32px;
 	margin:0 auto;
 	background:-webkit-linear-gradient(left, #e68629 , #f06d15);
 	line-height:32px !important;
 	margin-top:3%;
 	cursor: pointer;
 }
 .download:hover{
 	opacity: 0.8;
 }
 .download a{
 	color:#fff;
 }
</style>


