<template>
    <div class="pagination-wrap cf">
        <span class="total">找到<i>{{total?total:0}}</i>条数据</span>
        <el-button  slot class="pagenation-btn">确定</el-button>
        <el-pagination
                :page-size="pageSize"
                :layout="layout"
                :page-count="pageCount"
                @current-change="handleCurrentChange"
                @size-change="handleSizeChange"
        >

        </el-pagination>

	</div>
</template>

<script>
  export default {
    computed: {
      hasBtn(){
        return this.layout.indexOf('jumper') > 0
      }
    },
    data () {
      return {
        
      }
    },
    props: {
      pageSize: { //每页条数
        type: Number,
        default: 10
      },
      total: {  //总页数
        //type: Number,
        default: 100
      },
      pageCount: {
        type: Number,
        default: 10
      },
      layout: {
        type: String,
        default: "jumper, next, pager, prev"
      }
    },

    methods: {
      handleCurrentChange(val){
        this.$emit('pageChange', val)
      },
      handleSizeChange(val){
        console.log(`每页${val}条`)
      }
    }

  }

</script>

<style scope>

   .pagination-wrap {
        margin: 20px 0;
    }
    .total {
        float: left;
        margin-left: 24px;
        line-height: 32px;
        font-size: 14px;
        color: #fff;
    }
    .total i {
        padding: 0 5px;
        color: #f39f1d;
    }
    .el-pager li {
        width: auto;
        height: 16px!important;
        margin-top: 3px !important;
        padding: 2px;
        line-height: 15px!important;
        color: #bad8ff;
        border: 0 !important;
        background: transparent!important;
        vertical-align: middle!important;
    }

    .el-pager li.active {
        min-width: 20px !important;
        height: 20px !important;
        margin-top: 4px!important;
        padding: 2px;
        border: 1px solid rgba(21, 120, 255, .6);
        border-radius: 3px;
        background: -webkit-linear-gradient(top, #299dff, #0376dc)!important;
    }

    .el-pagination button.disabled {
        background: transparent;
    }

    .el-pagination__editor {
        height: 30px;
        line-height: 30px;
        border: 1px solid #0355c9 !important;
        border-radius: 5px!important;
        background: rgba(116, 160, 227, .33) !important;
        color: #bad8ff !important;
    }

    .pagenation-btn {
        float: right;
        width: 65px!important;
        height: 30px !important;
        padding: 0 5px;
        margin: 2px 0 0 9px!important;
        border: none !important;
        background: #ec771c;
        background: -webkit-linear-gradient(left, #e68629, #f06d16);
        border-radius: 3px;
        font-size: 12px;
        color: #fff;
        outline: none;
        cursor: pointer;
        line-height: 30px !important;

    }
    .pagenation-btn:hover {
        background: #f06e16;
    }
    .pagenation-btn span{
    	color:#fff;
    }

    .el-pagination .btn-next,
    .el-pagination .btn-prev {
        width: 30px;
        height: 30px;
        line-height: 30px;
        border: 1px solid #0355c9 !important;
        border-radius: 5px!important;
        background: rgba(116, 160, 227, .33)!important;
        color: #bad8ff !important;
    }
    .el-pagination button.disabled {
        background: transparent;
    }
    .el-pagination .btn-next:hover,
    .el-pagination .btn-prev:hover {
        background: rgba(116, 160, 227, .13)!important;
    }
    .el-pagination .btn-next.disabled:hover,
    .el-pagination .btn-prev.disabled:hover {
        background: none!important;
    }

    .el-pager {
        height: 28px;
        margin: 0 10px!important;
        padding: 0 20px!important;
        border: 1px solid #0355c9 !important;
        border-radius: 5px;
        background: rgba(116, 160, 227, .33) !important;
    }
    .el-pagination button,
    .el-pagination span {
        color: #bad8ff !important;
    }
    .el-pagination__jump,
    .btn-next,
    .el-pager,
    .btn-prev {
        float: right;
    }
    .el-pager li.btn-quicknext,
    .el-pager li.btn-quickprev {
        line-height: 16px;
    }
</style>



