<template>
	 <main class="main-list">
	 	<div class='main-top'>
	 		<div class="title">
	    		<p>企业360°画像</p>
	    		<div class='search'>
	    			<input type="text" class='txt' placeholder="请输入企业名称 / 统一社会信用代码 / 注册号" v-model="txt" @keydown.enter='search' @input='inputFun'/>
	    			<i :class={hide:boo} @click='clearBoth'>×</i>
	    			<el-button type="primary" icon="search" @click='search'></el-button>
	    		</div>
	    	</div>
	 	</div>
    	
    	<div class='list-empty' v-if='flag'>暂无数据</div>
    	<div class="list" v-else  v-loading="load">
    		<table border="" cellspacing="0" cellpadding="0" width="100%">
    			<thead class='thead'>
    				<tr>
	    				<td style='width:328px'>企业名称</td>
	    				<td style='width:340px'>统一社会信用代码/注册号</td>
	    				<td style='width:100px'>地区</td>
	    				<td style='width:100px'>法人</td>
	    				<td style='width:130px'>成立日期</td>
	    				<td style='width:150px'>企业360°画像</td>
	    			</tr>
    			</thead>
    			<tbody class='tlist'>
	    			<tr v-for='item in listData'>
	    				<td v-text='item.entName'><span></span></td>
	    				<td v-text='item.societyCode'></td>
	    				<td v-text='item.entAddress' style='padding-right: 10px'></td>
	    				<td v-text='item.legalPerson'></td>
	    				<td v-text='item.registerDate'></td>
	    				<td><span class='tb' @click=fn(item.entId,item.entName)><i></i>企业画像</span></td>
	    			</tr>
	    		</tbody>
    		</table>
    		<vpagination :total='total' :pageCount="pageCount" @pageChange='pageChange'></vpagination>
    	</div>
		<Masker :val='maskData'></Masker>
    </main>
</template>

<script>
import Vpagination from '../share/pagination.vue'
import Masker from '../share/mask.vue'
import axios from 'axios'
export default {
    name: 'listMain',
    data () {
    	return {
	    	txt:'',
          	maskData:{},
          	listData:[],
			boo:true,
			total:1,
			pageCount:1,
			pageSize:10,
			pageNum:1,
			load:true,
			val:'',
			flag:false
		}
	},
	mounted:function(){
		let n=this.$route.params.data;
		document.querySelector('#app').classList.add('app2');
		
		this.txt=n?n:localStorage.getItem("cont");
		this.maskData={flag:true,params:this.txt}
		this.boo=false;
		this.query(this.pageNum);
		if(localStorage.getItem('state')==1){
			this.$router.push({path:'/profile/'+localStorage.getItem("cont")})
			localStorage.removeItem('state');
		}
	},
	components:{
		Vpagination,
		Masker
	},
	methods:{
		fn:function(m,n){
			this.maskData={flag:false,params:this.txt,id:m,name:n}
			localStorage.setItem("state",1)
		},
		search(){
			//if(localStorage.getItem('cont')==this.txt){return};
			if(!this.txt.trim().length)return;
			localStorage.setItem('cont',this.txt)
			this.$router.push({path:'/profile/'+localStorage.getItem("cont")})
			this.query(this.pageNum)
		},
		query(n){
			let vm=this;
			this.load=true;
			vm.flag=false;
			let params={'companyInfo':this.txt,"page":n,"pageSize":this.pageSize};
			axios.get('/portrait/entPortrait/search',params)
				.then(res=>{
					if(res.data.code!=501){
						vm.load=false;
						vm.total=res.data.totalRecord;
				  		vm.pageCount=Math.ceil(vm.total/vm.pageSize)
						vm.listData=res.data.data;
					}else{
						vm.flag=true
					}
				})
		},
		inputFun(){
			if(this.txt.trim()){
				this.boo=false;
			}
		},
		clearBoth(){
			this.txt='';
			this.boo=true;
		},
		pageChange(n){
			this.query(n)
		}
	}
}
</script>

<style scoped>
.main-list{
	width: 1200px;
	min-height: calc(100vh - 191px);
  	margin: 0 auto;
}
.main-top{
	padding:20px 0;
}
 .title{
 	height:162px;
	 background: rgba(102,175,255,.2);
 	display:-webkit-box;
 	-webkit-box-align: center;
 	border-radius: 10px;
 }
 .title p{
 	width:312px;
 	font-size:28px;
 	color:#d2efff;
 	text-align: center;
 	font-weight: normal;
 }
 .search{
 	width:684px;
 	position: relative;
 }
 .search input:hover{
 	border-color:#39a7ef;
 }
 .search i{
    position: absolute;
    font-size: 22px;
    color: #469cd4;
    right: 14%;
    top: 6px;
    width: 40px;
    text-align: right;
    cursor: pointer;
 }
 .search .txt{
	width:552px;
 	height:44px;
 	float:left;
 	outline: none;
 	border:1px solid #469cd4;
 	border-right:none;
 	background: rgba(10,56,114,.8);
 	padding:0 20px;
 	color: #fff;
	font-size: 14px;
 	border-radius:5px 0 0 5px;
 }
 input::-webkit-input-placeholder{
    color: #fff;
}
 .el-button{
 	float: left;
 	width:91px;
 	height:46px;
 	border-radius: 0 4px 4px 0;
 	background: -webkit-linear-gradient(left, #e68629 , #f06e16); /* Safari 5.1 - 6.0 */
 	font-size:28px;
	padding: 5px 10px 0 10px;
 }
 .el-button--primary{
 	border-color:#e88124 !important;
 }
 .list{
 	min-height:585px;
 	padding-bottom:30px;
 }
 .list-empty{
 	background: rgba(102,175,255,.4);
 	line-height:3;
 	font-size:18px;
 	color:#fff;
 	text-align: center;
 }
 table{
 	font-size:14px;
 	color:#fff;
 	border: 1px solid transparent;
 	border-radius: 10px;
 	overflow: hidden;
 	min-height:100px;
 	table-layout:fixed;
 }
 .thead tr{
 	height:50px;
 	background: rgba(102,175,255,.4);
 	font-weight: bold;
 }
.thead tr td:first-child,
.tlist tr td:first-child {
	padding-left: 30px;
}
 .tlist tr{
 	height:60px;
 	border:1px solid #0c1849;
 	background: rgba(102,175,255,.15);
 }
 .tlist tr:hover{
 	background:#0d225c;
 }
 tr th,tr td{
 	border:none;
 	white-space: nowrap;
 	overflow: hidden;
 	text-overflow: ellipsis;
 }
 .tb{
 	width:114px;
 	height:32px;
 	display:inline-block;
 	border:1px solid #245de0;
 	border-radius: 4px;
	background-color: rgba(102,175,255,.25);
	line-height: 32px;
 	cursor: pointer;
 }
.tb i {
	float: left;
	width: 20px;
	height: 20px;
	margin: 7px 9px 0 14px;
	background: url(../../../static/img/bt360.png) no-repeat;
}
.tlist tr:hover .tb {
	border: 1px solid #2668ff;
	background-color: rgba(102,175,255,.5);
}
 .bg{
 	background:#406fb1;
 }
 .hide{
 	display: none;
 }

 .el-button--primary.is-active, .el-button--primary:active,.el-button--primary:focus, .el-button--primary:hover{
	 	background: -webkit-linear-gradient(left, #D9800F, #E36717);
	 }

</style>




