<template>
	<main class="main-search">
    	<div class="title">
    		<p>企业360°画像</p>
    		<div class='search'>
    			<input type="text" class='txt' autofocus v-model='txt' placeholder="请输入企业名称 / 统一社会信用代码 / 注册号" @keydown.enter='goListPage' @input='inputFun'/>
    			<i :class={hide:boo} @click='clearBoth'>×</i>
    			<el-button type="primary" icon="search" @click='goListPage'></el-button>
    			
    		</div>
    	</div>
    </main>
</template>

<script>
	export default {
    name: 'indexMain',
    data () {
    	return {
    		txt:'',
	    	boo:true,
	    	num:1
	    }
	},
	mounted:function(){
		document.querySelector('#app').classList.remove('app2');
	},
	methods:{
		goListPage:function(){
			if(!this.txt.trim().length)return;
			if (window.localStorage) {
			    localStorage.setItem("cont", this.txt);	
			}	
			this.$router.push({path:'profile/'+this.txt})
		},
		inputFun(){
			if(this.txt.trim()){
				this.boo=false;
			}
		},
		clearBoth(){
			this.txt='';
			this.boo=true;
		}
	}
  }
</script>

<style scoped>
	.main-search{
		width: 1200px;
		min-height: calc(100% - 191px);
		margin: 0 auto;
	}
	.title{
		margin-top:111px;
	}
	.title p{
	 	font-size:50px;
	 	margin:0 0 60px 0;
		padding-top: 120px;
	 	color:#d2efff;
	 	text-align: center;
	 	font-weight: normal;
	 	font-family: 'PingFang SC Regular','Lantinghei SC';
	 }
	 .search{
	 		width:886px;
	 		margin:0 auto;
		    overflow: hidden;
	 		position: relative;
	 }
	 .search .txt{
	 		box-sizing: border-box;
		 	width:710px;
		 	height:56px;
		 	float:left;
		 	outline: none;
		 	border:1px solid #469cd4;
		 	border-right:none;
		 	background: rgba(10,56,114,.9);
		 	padding:0 25px;
		 	color: #fff;
	    	font-size: 18px;
		    line-height: 56px;
	    	border-radius:5px 0 0 5px;
	 }
	 .search .txt:hover{
	 	border-color:#39a7ef;
	 }
	 .search i{
	 	position: absolute;
	 	font-size:28px;
	 	color:#469cd4;
	 	right: 21%;
	    top: 5px;
	    width: 50px;
	    height: 50px;
	    text-align: right;
	    cursor: pointer;
	 }  
	 input::-webkit-input-placeholder{
	    color: #fff;
	    font-size: 18px;
	    opacity:1;
	}
	 .el-button{
	 	float: left;
	 	width:125px;
	 	height:56px;
	 	border-radius: 0 4px 4px 0;
	 	background: -webkit-linear-gradient(left, #e68629 , #f06e16); /* Safari 5.1 - 6.0 */
	 	font-size:33px;
	 	
	 }
	.el-button:hover {
		background: -webkit-linear-gradient(left, #d98027, #e36714);
	}
	 .el-button--primary{
	 	border-color:#e88124 !important;
	 }
	 .el-button--primary.is-active, .el-button--primary:active,.el-button--primary:focus, .el-button--primary:hover{
	 	background: -webkit-linear-gradient(left, #D9800F, #E36717);
	 }
	 .hide{
	 	display: none;
	 }
</style>


