<template>
	<div class="ent-info">
		<div class="list-empty" v-if='data==""'>
			<p v-loading="load"></p>
		</div>
		<div v-else>
			<h3>2、工商基本信息</h3>
			<ol class='list'>
				<li><span class="cl">中文名称 :</span><span class="cr" v-text='data.entName'></span></li>
				<li><span class="cl">英文名称 :</span><span class="cr" v-text='data.entEngName'></span></li>
				<li><span class="cl">统一社会信用代码 :</span><span class="cr" v-text='data.societyCode'></span></li>
				<li><span class="cl">注册号 :</span><span class="cr" v-text='data.registerNum'></span></li>
				<li><span class="cl">法定代表人 :</span><span class="cr" v-text='data.legalPerson'></span></li>
				<li><span class="cl">注册资本 :</span><span class="cr" v-text='data.regAsset'></span></li>
				<li><span class="cl">企业类型 :</span><span class="cr" v-text='data.entType'></span></li>
				<li><span class="cl">成立日期 :</span><span class="cr" v-text='data.registerDate'></span></li>
				<li><span class="cl">登记状态 :</span><span class="cr" v-text='data.registerState'></span></li>
				<li><span class="cl">注册地址 :</span><span class="cr" v-text='data.entAddress'></span></li>
				<li><span class="cl">营业期限 :</span><span class="cr" v-text='data.termOfValidity'></span></li>
				<li><span class="cl">经营范围 :</span><span class="cr" v-text='data.businessScope'></span></li>
				<li><span class="cl">登记机关 :</span><span class="cr" v-text='data.registerOrganization'></span></li>
				<li><span class="cl">核准日期 :</span><span class="cr" v-text='data.approvalDate'></span></li>
			</ol>
		</div>
	</div>
</template>

<script>
	export default {
	    name: 'entInfo',
	    data () {
	    	return {
		    	data:"",
		    	load:true
		    }
		},
		props: {
            room: {
                type: Object,
                default() {
                    return {}
                }
            }
        },
        mounted:function(){
        	let vm=this;
        	this.$watch('room',function(){
	        	vm.data=vm.room.data;
        	})
        }
  	}
</script>

<style scoped>

.ent-info{
	padding-top:20px;
	height:100%;
	overflow-y: auto;
	position: relative;
}
.ent-info h3{
	font-size:18px;
	line-height:50px;
}
.list-empty{
  	width:100%;
}
.list-empty p{
	position: absolute !important;
	top:50%;
	left: 50%;
}
.list{
	padding:10px 0 20px 50px;
}

.list li{
	line-height:30px;
	overflow: hidden;
}
.list span{
	float:left;
}
.list .cl{
	width:22%;
}
.list .cr{
	width:78%;
}
</style>


