<template>
	<div class="chang-info">
		
		<h3>3、变更信息</h3>
		<div class='chang-box'>
			<p class="list-empty" v-if='listData==""'>暂无数据</p>
			<div class="list">
	    		<table border="" cellspacing="0" cellpadding="0" width="100%">
	    			<thead class='thead'>
	    				<tr>
		    				<th style='width:56px'>序号</th>
		    				<th style='width:120px'>变更事项</th>
		    				<th style='width:120px'>变更日期</th>
		    				<th style='width:266px'>变更前内容</th>
		    				<th style='width:266px'>变更后内容</th>
		    			</tr>
	    			</thead>
	    			<tbody class='tlist'>
		    			<tr v-for='(item,i) in listData'>
		    				<td v-text='i+1' class='tc'></td>
		    				<td><span v-text='item.item' class='ts text'></span></td>
		    				<td v-text='item.time'></td>
		    				<td><p class="td" v-text='item.beCont'></p></td>
		    				<td><p class="td" v-text='item.afCont'></p></td>
		    			</tr>
		    		</tbody>
	    		</table>
	    	</div>
		</div>
	</div>
</template>

<script>
	export default {
	    name: 'entChangeInfo',
	    data () {
	    	return {
		    	listData:""
		    }
		},
		props: {
            room: {
                type: Object,
                default() {
                    return {}
                }
            }
        },
		mounted:function(){
        	let vm=this;
        	this.$watch('room',function(){
	        	vm.listData=vm.room.data;
        	})
        }
  	}
</script>

<style scoped>
.chang-info{
	height: 100%;
	position: relative;
}
.chang-info h3{
	font-size:18px;
	line-height:54px;
	padding-top: 20px;
}
.chang-box{
	height: calc(100% - 74px);
	overflow: auto;
}
.list-empty{
  	position: absolute;
  	left: 50%;
  	top: 50%;
  	transform: translate3d(-50%,-50%,0);
  	font-size:18px;
	
}
table{
	margin: 10px 0;
	font-size:14px;
	color:#fff;
	border: 1px solid transparent;
}
.thead tr{
	height:36px;
	background: rgba(50,83,161,1);
	font-weight: bold;
}
.tlist tr{
	height:30px;
	border:1px solid #0c1849;
	background: rgba(50,83,161,.02);
}

tr th{
	border:none;
	text-indent: 14px;
}
tr td{
	text-align: center;
}
tr .td{
	padding:5px;
	text-indent: 28px;
	text-align: left;
	line-height: 2;
}
.change-info {
	width: 230px;
	padding: 5px;
	line-height: 24px;
	text-align: left;
	display: -webkit-box;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: 3;
	overflow: hidden;
	text-indent: 0;
}
.tc{
	text-indent: 1px;
}
.ts{
	display: block;
	width:120px;
}
.text{
	text-overflow:ellipsis;
	overflow:hidden;
	white-space:nowrap;
}
.tp{
	display: inherit;
}
</style>


