<template>
	<div class="whole-portrait">
		<h3>1、整体画像</h3>
		<div id="myChart" :style="{width: '100%', height: '472px'}"></div>
	</div>
	
</template>

<script>
	// 引入基本模板
	let echarts = require('echarts/lib/echarts')
	// 引入雷达图组件
	require('echarts/lib/chart/radar')
	// 引入提示框
	require('echarts/lib/component/tooltip')
	
	export default {
	    name: 'entPortrait',
	    data () {
	    	return {
		    	dataArr:[],
		    }
		},
		created(){
			let vm= this;
			if(this.room==undefined)return;
			this.$watch('room',function(){
				this.room.data.forEach(function(v){
					vm.dataArr.push(v.value)
				})
				if(this.dataArr.length>0){//alert(1)
					this.drawLine();
				}
			})
			
		},
		props: {
            room: {
                type: Object,
                default() {
                    return {}
                }
            }
        },
		methods: {
		    drawLine(){
		        // 基于准备好的dom，初始化echarts实例
		        let myChart = echarts.init(document.getElementById('myChart'))
		        // 绘制图表
		        myChart.setOption({
		            backgroundColor:'#253d76',
				    tooltip: {},
				    radar: {
				        shape: 'circle',
				        indicator: [
				           { name: '竞争风险', max: 100},
				           { name: '经营风险', max: 100},
				           { name: '偿债风险', max: 100},
				           { name: '舆情风险', max: 100},
				           { name: '合规风险(诉讼处罚)', max: 100},
				        ],
				        name:{
				            textStyle:{
				                color:'#fff'
				            }
				        },
				        axisLine: {
				            lineStyle: {
				                color: '#fff',
				                opacity:0.2
				            }
				        },
				       
				        splitLine:{
				            lineStyle:{
				                color:'transparent'
				            }
				        },
				        splitArea:{
				            areaStyle:{
				                color:['#0e336d','#10234a']
				            }
				        }
				    },
				  
				    series: [{
				        name:'风险预估',
				        type: 'radar',
				       
				        // areaStyle: {normal: {}},
				        
				        data : [
				            {
				                value : this.dataArr,
				                name : '',
				                symbol: 'circle',
				                symbolSize:8,
				                itemStyle:{
				                    normal:{
				                        color:'#00a6ff',
				                        lineStyle:{
				                            color:'#00a6ff'
				                        },
				                        areaStyle:{
				                            color:'#00a6ff'
				                        }
				                    }
				                }
				            }
				        ]
				    }]

		        });
		    }
		}
  	}
</script>

<style scoped>
	.whole-portrait{
		padding:20px 0 0 0;
	}
	.whole-portrait h3{
		font-size:18px;
		line-height:3;
	}
</style>


