import Vue from 'vue'
import Router from 'vue-router'
import indexVue from '@/components/index'
import IndexMain from '@/components/pages/indexMain.vue'
import ListMain from '@/components/pages/listMain.vue'

//弹出框子页
import entPro from '@/components/pages/childs/entProfile.vue'
import entCata from '@/components/pages/childs/entCatalog.vue'
import entPor from '@/components/pages/childs/entPortrait.vue'
import entInfo from '@/components/pages/childs/entInfo.vue'
import entChangeInfo from '@/components/pages/childs/entChangeInfo.vue'
import entEnd from '@/components/pages/childs/endOfPreview.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/home',
      name: 'index',
      component: indexVue,
      children:[{
	      	path:'/search',
	      	component:IndexMain
	     },
	     {
	      	path:'/list',
	      	component:ListMain,
	      	children:[
	      		{
	      			path:'/profile/:data',
	      			component:entPro
	      		},
	      		{
	      			path:'/catalog',
	      			component:entCata
	      		},
	      		{
	      			path:'/portrait',
	      			component:entPor
	      		},
	      		{
	      			path:'/info',
	      			component:entInfo
	      		},
	      		{
	      			path:'/change',
	      			component:entChangeInfo
	      		},
	      		{
	      			path:'/end/:id/:name',
	      			component:entEnd
	      		},
	      		{
				     		path:'',
								redirect:'/profile/:data'
				    }
	      	]
	     },{
	     		path:'',
					redirect:'/search'
	     }
      ]
    },
    { path: "/", redirect: "/home" }//重定向,指向了home组件  
  ]
})



