import Mock from 'mockjs';
//console.log(Mock);
Mock.mock('/portrait/entPortrait/search',{
	"code":"1",
    "msg":"成功",
    "totalRecord":"1025",
	'data|10':[{
        "entId":"@id",
        "entName":"@ctitle",
        "societyCode":"@id",
        "entAddress":"@city",
        "legalPerson":"@cname",
        "registerDate":"@date",
        "entBaseInfo":"企业360°画像",
        "isMarket":"是"
    }]
})

Mock.mock('/portrait/entPortrait/getEntBaseInfo',{
	"code":"1",
    "msg":"成功",
    'data':
	{
        "entName":"@ctitle",
        "societyCode":"@id",
        "fileSize":"@float(5,50,1,2)"+"KB"
    }
})

Mock.mock('/portrait/entPortrait/wholePortrait',{
	"code":"1",
    "msg":"成功",
    'data|4':[
	 {
        "name":"@ctitle",
        "value":"@natural(10, 99)"
     }]
})

Mock.mock('/portrait/entPortrait/basicInfo',{
	"code":"1",
    "msg":"成功",
    'data':{
        "entName":"@ctitle",
        "entEngName":"@first",
        "societyCode":"@id",
        "registerNum":"@id",
        "legalPerson":"@cname",
        "regAsset":"@string('number', 7)"+'圆整',
        "entType":"@ctitle",
        "registerDate":"@date",
        "registerState":"开业|未开业 登记状态",
        "entAddress":"@county(true)",
        "termOfValidity":"@date",
        "businessScope":"@csentence",
        "registerOrganization":"@region",
        "approvalDate":"@date"
     }
})

Mock.mock('/portrait/entPortrait/changeInfo',{
	"code":"1",
    "msg":"成功",
    'data|4':[
	 {
        "item":"经营范围",
        "time":"@date",
        "beCont":"@csentence",
        "afCont":"@cparagraph"
     }]
})



