<template>
	<div class="company-profile">
		<div v-if="room.code!=1">
			<p v-loading="load"></p>
		</div>
		<ul v-else>
			<li><span class="con-fl">企业名称 :</span><span class="con-fr" v-text="room.data.entName"></span></li>
			<li><span class="con-fl">生成日期 :</span><span class="con-fr" v-text='time'></span></li>
			<li><span class="con-fl">统一社会信用代码 :</span><span class="con-fr" v-text="room.data.societyCode"></span></li>
		</ul>
	</div>
</template>

<script>
	export default {
	    name: 'entProfile',
	    data () {
	    	return {
		    	time:'',
		    	load:true
		    }
		},
		mounted:function(){
			let t=new Date().toLocaleDateString().split('/').join('-')
			this.time=t
		},
		props: {
            room: {
                type: Object,
                default() {
                    return {}
                }
            }
        }
  	}
</script>

<style scoped>
.company-profile{
 	height:100%;
 	display:-webkit-box;
 	-webkit-box-align:center;
 	-webkit-box-pack:center;
 }
 .company-profile div{
 	width:100%;
 }
 .company-profile ul{
 	width:50%;
 }
 .company-profile ul li{
 	font-size: 21px;
 	color:#fff;
 	line-height: 2;
 	overflow:hidden;
 }
 .con-fl,.con-fr{
 	float:left;
 	width:50%;
 }
 
</style>


