<template>
	<div :class='{hide:val.flag}'>
		<div class="mask" id="mask" @click='fnMask'>
		
		</div>
		<div class="mask-box" >
			<div class="mask-top">
				<p class='fl-title'>
					<span class="fl-con">企业360°画像</span>
					<span class='fr-con' v-text='val.name'></span>
				</p>
				<p class='fr-title'>
					<b v-text="size">13.20MB</b>
					<span class='down-img' @click='down'><!--<a :href='address' download='zl' id='down'>-->下载图像 ￥20.20 <!--</a>--><i></i></span>
				</p>
				<img src="../../../static/img/11.png" alt="" class='img'/>
			</div>
			<div class="mask-con">
				<div class='left-btn btn el-icon-arrow-left' @click="prev" :class='{gray:this.num==1}'></div>
				<div class="content">
					<router-view :room="room"></router-view>
				</div>
				<div class='right-btn btn el-icon-arrow-right' @click="next" :class='{gray:this.num==6}'></div>
			</div>
			<div class="mask-bottom">
				<p><span class='span-fir' v-text='num'></span> / <span v-text='sum'></span></p>
			</div>
		</div>
		<div class="loading" :class="{hide:!load}"  v-loading="load" v-bind:element-loading-text="text"></div>
		<iframe src="" frameborder="0" ref='iframe' class="hide"></iframe>
	</div>
	
</template>

<script>

export default {
    name: 'listMain',
    data () {
    	return {
	    	num:1,
	    	sum:12,
	    	flag:true,
	    	endId:'',
	    	room:{},
	    	state:false,
	    	address:'',
	    	record:false,
	    	load:false,
	    	text:"拼命加载中",
	    	size:'0KB'
	    }
	},
	props:['val'],
	updated:function(){
		if(this.state){return};
		if(!this.val.flag && this.num==1){
			this.queryInfo(this.val.id,0,this.val.name)
		}
	},
	mounted:function(){
		this.$ajax.defaults.timeout=30000;
	},
	methods:{
		
		fnMask:function(){
			this.val.flag=true;
			let params=localStorage.getItem("cont");
			this.$router.push({path:'/profile/'+params});
			this.num=1;
			this.state=false;
			this.room={};
			this.load=false;
			this.size="0KB"
		},
		down(){
			//
			let vm=this;
			//加载动画
			this.load=true;
			
			var iframe=this.$refs.iframe
			//http://202.106.10.250:18069 http://192.168.20.93:8069
			let src=`/portrait/entPortrait/downLoad/${this.val.id}/${this.val.name}`;
			iframe.src = src
			
			iframe.onload=function(){
				vm.text='下载失败';
			}
			//监听下载状态
			this.$ajax.get(src).then(function(res){
				//vm.text='下载成功';
				setTimeout(function(){vm.load=false},1200)
			},function(err){
				//vm.text='下载失败';
				setTimeout(function(){vm.load=false},1200)
			})
		},
		queryInfo(id,n,name){
			this.state=false;
			let vm=this;
			let url='';
			let baseurl='';   //http://192.168.20.93:8069  http://202.106.10.250:18069
			let baseurlMock='http://result.eolinker.com/txxudMb09dc24fd77ece85762915eee07f78a668ec06b9a?uri=';
			if(this.record){
				url="/portrait/entPortrait/getEntBaseInfo"
			}else{
				switch (n){
					case 0:url="/portrait/entPortrait/getEntBaseInfo";break;
					case 1:url="/portrait/entPortrait/wholePortrait";break;
					case 2:url='/portrait/entPortrait/basicInfo';break;
					case 3:url='/portrait/entPortrait/changeInfo';break;
				}
			}
			let params={'entId':id,"page":n,"entName":name};
		    vm.$ajax.get(url,params)
		    		.then((res)=>{console.log(res.data.data)
		    			if(res.data.data.fileSize){
				    		vm.size=res.data.data.fileSize;
				    	}
				    	vm.room=res.data;
				  		vm.state=true;
		    		})
		},
		next(){
			this.room={};
			if(this.num>=6){return;}
			this.num++;
			switch (this.num){
				case 2:this.$router.push({path:'/catalog'});break;
				case 3:this.$router.push({path:'/portrait'});this.queryInfo(this.val.id,1);break;
				case 4:this.$router.push({path:'/info'});this.queryInfo(this.val.id,2);break;
				case 5:this.$router.push({path:'/change'});this.queryInfo(this.val.id,3);break;
				case 6:this.$router.push({path:'/end/'+this.val.id+'/'+this.val.name});break;
			}
		},
		prev(){
			if(this.num<=1){return};
			this.num--;
			this.room={};
			switch (this.num){
				case 1:this.$router.push({path:'/profile/'+this.val.params});this.queryInfo(this.val.id,0);break;
				case 2:this.$router.push({path:'/catalog'});break;
				case 3:this.$router.push({path:'/portrait'});this.queryInfo(this.val.id,1);break;
				case 4:this.$router.push({path:'/info'});this.queryInfo(this.val.id,2);break;
				case 5:this.$router.push({path:'/change'});this.queryInfo(this.val.id,3);break;
            }
		}
	}
}
</script>

<style scoped>

.mask{
	width:100%;
	height:100%;
	background:#000;
	position:absolute;
	top:0;
	left:0;
	opacity:0.5;
	font-size:14px;
 }
 .mask-box,.loading{
 	width:950px;
 	height:620px;
 	background:#253d76;
 	position:absolute;
 	left:50%;
 	top:50%;
 	margin-left: -475px;
 	margin-top:-310px;
 	opacity:1;
 }
 .loading{
 	position: absolute !important;
 }
 .mask-top{
 	height:50px;
 	width:100%;
 	background: rgba(58,102,178,.5);
 	position: absolute;
 	top:0;
 	left: 0;
 	line-height:50px;	
 	color:#fff;
 	z-index:3;
	 box-shadow: 0 2px 10px 0 rgba(15,26,51,.68);
 }
 .img{
 	position: absolute;
 	right:0;
 }
 .fl-title{
 	float:left;
 }
 .fl-con{
 	font-size:18px;
 	padding:0 28px;	
 }
 .fr-con{
	 font-size: 14px;
 	color:#fff;
 }
 .fr-title{
 	float:right;
 }
 .down-img{
 	margin:10px 32px 0 20px;
 	width:146px;
 	height:30px;
 	display:inline-block;
 	border:1px solid rgba(255,186,0,.75);
	 border-radius: 3px;
 	background: rgba(255,207,123,.15);
 	color:#ffd015;
 	line-height:30px;
 	text-align: center;
 	position:relative;
 	cursor: pointer;
 }
 .down-img i{
 	width:40px;
 	position: absolute;
 	border-top:2px solid #d4b633;
 	right:22px;
 	top:15px;
 }
 .down-img a{
 	color:#ffd015;
 	text-decoration: none;
 }
 .down-img:hover{
 	background: none;
 }
 .mask-bottom{
 	background:#0a2958;
 	height:48px;
 	width:100%;
 	color:#d4d9e6;
 	display:-webkit-box;
 	-webkit-box-align:center;
 	-webkit-box-pack:center;
 	position:absolute;
 	bottom:0;
 	left:0;
 }
 .span-fir{
 	background:#264282;
 	width:30px;
 	height:24px;
 	display: inline-block;
 	text-align:center;
 	line-height:24px;
 	border-radius: 3px;
 }
 .mask-con{
 	height:522px;
 	position: relative;
 	padding:50px 60px 0 60px;
 }
 .btn{
 	width:60px;
 	height:30px;
 	position: absolute;
 	top:54%;
 	text-align: center;
 	color: #fff;
 	font-size: 30px;
 	cursor:pointer;
 }
 .btn:hover{
 	color:#0A2958;
 }
 .gray:hover{
 	color:#808080;
 }
 .left-btn{
 	left:0;
}
.right-btn{
 	right:0;
}
.gray{
	color:gray;
	cursor: not-allowed;
}
.content{
	height:100%;
	color:#fff;
	font-size: 14px;
}

.hide{
	display: none;
}
</style>


